/* Home Page */
* {
  font-family: "Exo 2", sans-serif;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  background-image: url(./../pictures/background.jpg);
  /* background-color: black;*/
  display: flex;
  color: white;
}

.logoDedSec {
  width: 200px;
}

.section-main {
  justify-content: center;
  align-items: center;
  text-align: left;
}
/* Nav Bar */
.nav-main {
  background-color: rgba(5, 5, 5, 0.2);
  min-height: 10vh;
  display: flex;
  align-items: center;
  margin: auto;
  border-bottom: 2px solid rgba(5, 5, 5, 0.5);
  justify-content: space-between;
  width: 100%;
  height: auto;
  font-size: 20px;
  box-shadow: 0px 2px 4px 0px rgba(7, 7, 7, 0.2);
  /*-webkit-box-shadow:0px 5px 5px rgba(5, 5, 5, 0.5);
 -moz-box-shadow:0px 10px 10px rgba(5, 5, 5, 0.5);
 box-shadow:0px 20px 20px rgba(10, 10, 10, 0.5);*/
}
/*ul {
    list-style-type: none;
}*/
.nav-main ul {
  list-style-type: none;
  display: flex;
  justify-content: space-around;
  width: 50%;
}
.fa-user-circle {
  font-size: 30px;
}
.nav-main li {
  margin: 0 40% 0 25%;
  /* width: 40px; */
}
.nav-icons {
  /*padding: 0 2% 0 2%;*/
  margin-right: 1%;
  /* margin: 1%;*/
}
/*  */
/* White glow on hover (class="icon" and adding .icon:hover element to the css)*/
.icon i,
h2 {
  color: white;
  -moz-transition: all 0.2s ease-in;
  -o-transition: all 0.2s ease-in;
  -webkit-transition: all 0.2s ease-in;
  transition: all 0.2s ease-in;
}
.icon:hover i,
.icon:hover h2 {
  color: #ffffff;
  text-shadow: 0px 0px 30px #ffffff;
  -moz-transition: all 0.2s ease-in;
  -o-transition: all 0.2s ease-in;
  -webkit-transition: all 0.2s ease-in;
  transition: all 0.2s ease-in;
}
.fa-user-plus,
.fa-user-circle,
.fa-book-dead,
.fa-skull-crossbones {
  /* .fas, .far { */ /* Sets the color of all icons */
  color: white;
}
.to-home {
  margin-left: 1.5%;
}
/* Purple line when hovering over (class="cool-link" (add to html for a purple line on hover)*/
.cool-link {
  display: inline-block;
  color: white;
  text-decoration: none;
}
.cool-link::after {
  content: "";
  display: block;
  width: 0;
  height: 2px;
  background: white;
  transition: width 0.3s;
}
.cool-link:hover::after {
  width: 100%;
  transition: width 0.3s;
}
